* {
  font-family: "Roboto";
  font-weight: lighter;
  box-sizing: border-box;
}

nav {
  display: flex;
  width: 100%;
  justify-content: flex-start;
  align-items: center;
  background-color: white;
  color: black;
  padding: 0 20%;
}

.nav-logo {
  height: 50px;
  margin-right: 1rem;
}

body {
  margin: 0;
}

.announcement {
  width: 100%;
  background-image: linear-gradient(to right, #567BFF, #5EB1FF);
  color: white;
  margin-bottom: 1rem;
  padding: 1rem 15%;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 100;
}
.announcement h3 {
  margin: 0;
  width: 100%;
  text-align: center;
}
.announcement a {
  color: white;
}

.thumbnail-r {
  margin-left: 0.5rem;
  transition: all 0.1s ease-in-out;
}

.thumbnail-l {
  margin-right: 0.5rem;
  transition: all 0.15s ease-in-out;
}

span {
  font-size: medium;
}

.articles {
  padding: 0 20%;
}

.info {
  display: flex;
  align-items: center;
}
.info h2 {
  margin: 0;
}

.more-info {
  padding: 0 !important;
}

.button-div {
  width: 100%;
}

.img-div {
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-end;
  padding-left: 1rem;
  max-height: 100%;
  max-width: 100%;
}

.card-img {
  max-height: 100%;
  max-width: 100%;
  border-radius: 1rem;
  position: relative;
  right: 0;
}

@media only screen and (max-width: 1200px) {
  nav, .articles, .announcement {
    padding: 0 1rem;
    position: static;
  }

  .thumbnail-l, .thumbnail-r {
    width: 100%;
    margin-left: 0;
    margin-right: 0;
  }

  .info {
    margin-bottom: 1rem;
  }

  .img-div {
    justify-content: center;
    padding: 0;
  }
}/*# sourceMappingURL=style.css.map */
